/**
 *     : 30%, +65, 2s, 5 seconds, .
 *    <span class="stat-highlight">  .
 */
export const highlightValues = (text: string): string => {
  return text.replace(
    /(\+?\d+(?:\.\d+)?\s*%|\+\d+(?:\.\d+)?|\d+(?:\.\d+)?\s*(?:seconds?|[]?|\.?|HP|hp|MP|mp|s\b))/g,
    '<span class="stat-highlight">$1</span>'
  );
};
